import React, { useState } from 'react';
import { Container, TextField, Typography, Button, Box } from '@mui/material';
import logo from '../logo.svg';

const LoginScreen = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [emailError, setEmailError] = useState('');

  const validateEmail = (email) => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
  };

  const handleEmailChange = (email) => {
    setEmail(email);
    setEmailError(email === '' || validateEmail(email) ? '' : 'Email is invalid');
  };

  return (
    <Container maxWidth="xs" sx={{ textAlign: 'center', backgroundColor: '#0D232E', padding: 4, borderRadius: 2, color: '#FFFFFF' }}>
      <Box component="img" src={logo} alt="logo" sx={{ width: 200, mb: 3 }} />
      <Typography variant="h4" gutterBottom>Login</Typography>
      <TextField
        label="Email"
        value={email}
        onChange={(e) => handleEmailChange(e.target.value)}
        fullWidth
        margin="normal"
        variant="outlined"
        error={!!emailError}
        helperText={emailError}
        sx={{ backgroundColor: '#1B2C3B', color: '#FFFFFF', input: { color: '#FFFFFF' } }}
      />
      <TextField
        label="Password"
        value={password}
        onChange={(e) => setPassword(e.target.value)}
        fullWidth
        margin="normal"
        variant="outlined"
        type="password"
        sx={{ backgroundColor: '#1B2C3B', color: '#FFFFFF', input: { color: '#FFFFFF' } }}
      />
      <Button variant="contained" color="primary" sx={{ backgroundColor: '#4A90E2', mt: 3 }} fullWidth>
        Login
      </Button>
    </Container>
  );
};

export default LoginScreen;
